<template>
    <div class="order-comment">
        <div class="comment-content">
            <div class="comment-item" v-for="(item,index) in commentOrderInfo">
                <div class="left-box">
                    <div class="info">
                        <img :src="item.smallImg" alt="" class="img">
                        <p class="order-info">{{item.chrName}}</p>
                        <div class="block">
                            <span class="demonstration">{{$L('综合评分')}}</span>
                            <el-rate v-model="item.meanScore" disabled></el-rate>
                        </div>
                    </div>
                </div>
                <div class="right-box">
                    <el-form ref="form" label-width="110px">
                        <el-form-item :label="$L('评价商品')">
                            <el-input :rows="5" maxlength="500" resize="none" type="textarea" v-model="item.prodText" class="desc" @change="inputChange(item.prodId)"></el-input>
                            <span class="desc-num">{{item.prodText ? item.prodText.length:0}}/500</span>
                        </el-form-item>
                        <el-form-item :label="$L('晒图')">
                            <uploadPic @UpLoadPic="getImg" v-bind:id="item.prodId"></uploadPic>
                        </el-form-item>
                    </el-form>
                </div>
            </div>
            <div class="comment-bottom">
                <div class="left-box">{{ $L('网站动态评分') }}</div>
                <div class="right-box">
                    <p class="p"><span class="red">*</span>{{$L(' 宝贝与描述相符')}}<el-rate v-model="product"></el-rate></p>
                    <p class="p"><span class="red">*</span>{{$L(' 卖家的服务态度')}}<el-rate v-model="service"></el-rate></p>
                    <p class="p"><span class="red">*</span>{{$L(' 物流服务的质量')}}<el-rate v-model="logistics"></el-rate></p>
                </div>
            </div>
            <div class="btns">
                <el-button @click="backFunc">{{ $L('返回') }}</el-button>
                <el-button type="primary" @click="submitFunc">{{ $L('提交评价') }}</el-button>
            </div>
        </div>
    </div>
</template>

<script>
    import uploadPic from "../../../components/order/uploadPic";
    export default {
        name: "orderComment",
        components:{
            uploadPic
        },
        data () {
            return {
                loading:true,
                errorType: 'error',
                errorTitle: this.$L('出错了'),
                errorTips: null,
                errorShow: false,
                commentOrderInfo:null,
                product:5,
                service:5,
                logistics:5,
                score:[],
                addComment: null,
                evaluate:{},
                imgList:[],
                Eval:{
                    Product: [],
                    Service: []
                },
                prodText:'',
                idSpecPrice: [],
                idProduct: [],
            }
        },
        created() {
            let id = 0;
            if (this.$route.params.hasOwnProperty('id')) {
                id = this.$route.params.id;
                id = parseInt(id);
            }
            if (!isNaN(id) && id) {
                this.getData(id);
            } else {
                this.showError();
            }

        },
        methods:{
            backFunc() {
                this.$router.back({name:'orderList',params:{type:'all'}});
            },
            showError() {
                this.$alert(this.$L('订单不存在或已删除'), this.$L('参数有误'), {
                    confirmButtonText: this.$L('返回'),
                    type: 'error',
                }).then(() => {
                    this.backFunc();
                });
            },
            inputChange(prodId) {

                for (let i in this.commentOrderInfo) {
                    if (this.commentOrderInfo[i]['prodId'] == prodId) {
                        this.Eval.Product[prodId] = this.commentOrderInfo[i]['prodText'];
                        this.Eval.Service[prodId] = this.$L("非常好");
                    }
                }


            },
            getData(id) {
                const _self = this;
                _self.loading = true;
                _self.$api('commentOrderInfo', {orderId: id}, function (reData) {
                    if (reData.flag === 1) {
                        _self.commentOrderInfo = reData.data.commentList;
                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$gotoWebHome();
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('重试'),
                            cancelButtonText: _self.$L('返回'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData(id);
                        }).catch(action => {
                            _self.backFunc();
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('重试'),
                        cancelButtonText: _self.$L('返回'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData(id);
                    }).catch(action => {
                        _self.backFunc();
                    });
                });
            },
            //获取子组件的图片
            getImg(data,id) {
                this.imgList[id] = {
                    'fileList': data
                };
            },
            submitFunc(){

                const _self = this;
                _self.loading = true;
                for(let i in _self.commentOrderInfo){
                    _self.idSpecPrice[_self.commentOrderInfo[i]['prodId']] = _self.commentOrderInfo[i]['goodsSpecId'];
                    _self.idProduct[i] = _self.commentOrderInfo[i]['prodId'];
                    if(!_self.Eval['Product'][_self.commentOrderInfo[i]['prodId']]) {
                        _self.$message.error(_self.$L("请完善评论信息"));
                        return;
                    }

                }
                let id = _self.$route.params.id;
                _self.imgList[id] = {
                    'fileList':_self.uploadList
                };
                let arr = {
                    IDOrder:id,
                    Score: {
                        'Desc':_self.product,
                        'Service':_self.service,
                        'KuaiDi':_self.logistics
                    },
                    intShow: 1,
                    Eval : _self.Eval,
                    IDSpecPrice: _self.idSpecPrice,  
                    IDProduct: _self.idProduct,
                    'CommentsPic': {
                        fileList: _self.imgList
                    },

                };
                let postData = _self.$objToFormData(arr);
                _self.$api('addComment', postData, function (reData) {
                    if (reData.flag === 1) {
                        _self.$message({
                            type: 'success',
                            message: _self.$L('提交成功!')
                        });
                        _self.$router.push({name: 'checkComment' });
                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('取消'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$gotoWebHome();
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.$gotoWebHome();
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(action => {
                        _self.$gotoWebHome();
                    });
                }, true);

            },
        }
    }
</script>

<style lang="scss" scoped>
    @import "../../../assets/icon/iconfont.css";
    @import "../../../assets/css/common/lib.scss";
    .order-comment{
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        z-index: 100;
        padding: 20px !important;
        .comment-content{
            font-size: 14px;
            background-color: #fff;
            .comment-item{
                padding: 40px 0 40px 20px;
                border-bottom: 1px dashed #cccccc;
                overflow: hidden;
                .info{
                    text-align: center;
                    .order-info{
                        margin-top: 5px;
                        @extend .text-one-line;
                    }
                    .img{
                        width: 100px;
                        height: 100px;
                    }
                    .block{
                        .demonstration{
                            padding: 15px 0 5px;
                            display: inline-block;
                        }
                    }
                }
                .el-form-item{
                    padding: 0;
                    margin-bottom: 35px;
                    &:last-child{
                        margin-bottom: 0;
                    }
                    .el-form-item__label{
                        line-height: 1;
                        padding: 0 40px 0 0;
                    }
                    .desc{
                        font-size: 14px;
                        width: 80%;
                    }
                    .desc-num{
                        vertical-align: bottom;
                        line-height: 1;
                        padding-left: 2%;
                    }
                }
            }
            .comment-bottom{
                overflow: hidden;
                padding: 30px 20px;
                border-bottom: 1px dashed #cccccc;
                .p{
                    margin-bottom: 10px;
                    .el-rate{
                        display: inline-block;
                        vertical-align: middle;
                        margin-left: 10px;
                    }
                }
            }
            .btns{
                text-align: right;
                padding: 40px 30px;
                .el-button{
                    width: 90px;
                    height: 35px;
                }
            }
            .left-box{
                float: left;
                width: 250px;
                text-align: center;
            }
            .right-box{
                float: left;
                width: calc(96% - 250px);
                margin-left: 4%;
            }
            .red{
                color: #FF0000;
            }
        }
        .error {
            position: absolute;
            z-index: 200;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: #FFFFFF;
            overflow: hidden;

            .error-tips {
                position: absolute;
                top: 50%;
                left: 50%;
                width: 320px;
                transform: translate(-50%, -50%);
            }
        }
    }
</style>