<template>
    <div class="uploadPic">
        <div class="up-load-pic">
            <ul class="img-list">
                <li class="load-img" v-for="(i,k) in showImgArr" :key="k">
                    <div class="upload-list-cover">
                        <span class="bbx-font bbx-icon-shanchu" @click="handleRemove(k)"></span>
                    </div>
                    <img class="img" :src="i" />
                </li>
                <li class="load-img up-load-img" v-if="showImgArr.length < maxNum">
                    <span class="bbx-font bbx-icon-jiahao"></span>
                    <input type="file" name="CommentsPic" class="upload" multiple accept="image/png,image/jpeg,image/jpg" @change="upImgFunc"/>
                </li>
            </ul>
        </div>
        <span class="img-num">{{uploadLen}}/5</span>
    </div>
</template>

<script>
    export default {
        name: "uploadPic",
        props:{
            index:{
                type:Number,
            },
            id:{
                id:Number
            }
        },
        data(){
            return {
                maxNum: 5,
                uploadList: [],
                uploadLen: 0,
                showImgArr:[],
            }
        },
        methods:{
            //删除图片
            handleRemove(index){
                this.uploadList.splice(index, 1);
                this.showImgArr.splice(index, 1);
                this.uploadLen = this.showImgArr.length;
            },
            //上传图片
            upImgFunc(event) {
                const _self = this;
                let files = event.target.files;
                console.log(files);
                let oldLen = _self.uploadLen;
                let len = files.length+oldLen;
                if(len>_self.maxNum){
                    _self.$message({
                        showClose: true,
                        message:'最多可上传'+_self.maxNum+'张，您还可以上传'+(_self.maxNum-oldLen)+'张',
                        type: 'success'
                    });
                    return false;
                }
                for (let i=0; i < files.length; i++) {
                    let imgUrl = null;
                    let size = Math.floor(files[i].size / 1024);
                    let type = files[i].type.substring(6);
                    if (size > 2*1024*1024) {
                        _self.$message({
                            showClose: true,
                            message:'请选择2M以内的图片!',
                            type: 'warning'
                        });
                        return false;
                    }
                    if(!(type === 'png' || type === 'jpeg' || type === 'jpg')){
                        _self.$message({
                            showClose: true,
                            message:'请选择png、jpeg、jpg的图片格式！',
                            type: 'warning'
                        });
                        return false;
                    }
                    _self.uploadList.push({
                        'file':files[i]
                    });
                    _self.uploadLen++;
                    let reader = new FileReader();
                    reader.readAsDataURL(files[i]);
                    reader.onload = function(evt){
                        imgUrl = evt.target.result;
                        _self.showImgArr.push(imgUrl);
                    }
                }
                _self.$emit('UpLoadPic',_self.uploadList,_self.id);
            },
        }
    }
</script>

<style lang="scss" scoped>
    .uploadPic{
        .up-load-pic {
            display: inline-block;
            .img-list {
                overflow: hidden;
                .load-img{
                    width: 80px;
                    height: 80px;
                    line-height: 80px;
                    float: left;
                    text-align: center;
                    position: relative;
                    overflow: hidden;
                    margin-right: 20px;
                    &.up-load-img{
                        background-color: #F0F1F5;
                        text-align: center;
                        margin-right: 15px;
                    }
                    &:hover .upload-list-cover{
                        display: block;
                    }
                    .img{
                        width: 100%;
                        height: auto;
                    }
                    .upload-list-cover{
                        display: none;
                        position: absolute;
                        top: 0;
                        bottom: 0;
                        left: 0;
                        right: 0;
                        background: rgba(0,0,0,.6);
                        .bbx-font{
                            color: #fff;
                            font-size: 16px;
                            cursor: pointer;
                        }
                    }
                    .upload{
                        position: absolute;
                        top: 0;
                        left: 0;
                        width: 100%;
                        height: 100%;
                        opacity: 0;
                        cursor: pointer;
                    }
                }
            }
        }
        .img-num{
            line-height: 1;
        }
    }
</style>